<template>
  <div class="container">
    <!-- 头部导航组件 -->
    <navbar class="navbar">
      <div class="back" slot="back">
        <i class="iconfont iconjiantou2" @click="$router.back()"></i>
      </div>
      <h4 slot="title" class="title">个人中心</h4>
    </navbar>
    <div class="user_pic">
      <img :src="$axios.defaults.baseURL + userinfo.head_img" alt />
      <van-uploader :after-read="afterRead" />
    </div>
    <div class="user_info">
      <listbar :label="'昵称'" :tips="userinfo.nickname" @click.native="show = true"></listbar>
      <listbar :label="'密码'" :tips="'******'" @click.native="ispassword = true"></listbar>

      <listbar :label="'性别'" :tips="['女', '男'][userinfo.gender]" @click.native="showgender = true"></listbar>

      <!-- 编辑昵称的弹窗 -->
      <van-dialog v-model="show" title="修改昵称" show-cancel-button @confirm="changenickname">
        <van-field v-model="nickname" placeholder="请输入用户名" />
      </van-dialog>
      <!-- 编辑密码的弹窗 -->
      <van-dialog v-model="ispassword" title="修改密码" show-cancel-button @confirm="changepassword">
        <van-field v-model="password" type="password" placeholder="请输入要修改的密码" />
      </van-dialog>
      <!-- 修改性别的弹窗 -->
      <van-action-sheet
        v-model="showgender"
        :actions="actions"
        @select="onSelect"
        cancel-text="取消"
        close-on-click-action
      />
    </div>
  </div>
</template>

<script>
import navbar from "@/components/navbar.vue";
import listbar from "@/components/listbar.vue";

export default {
  data() {
    return {
      // 定义个人详情，初始值为对象
      userinfo: {},
      token: "",
      //   控制昵称弹窗是否显示
      show: false,
      ispassword: false,
      showgender: false,
      nickname: "",
      password: "",
      actions: [
        { name: "男", value: 1 },
        { name: "女", value: 0 }
      ]
    };
  },
  mounted() {
    const info = JSON.parse(localStorage.getItem("user_info")) || {};
    this.token = info.token;
    // 等组件加载完毕后只需请求
    this.$axios({
      url: `/user/${info.user.id}`,
      method: "get",
      // 添加验证token
      headers: {
        Authorization: this.token
      }
    }).then(res => {
      const { data } = res.data;
      this.userinfo = data;
      this.nickname = data.nickname;
    });
  },
  methods: {
    afterRead(file) {
      // 将文件上传至服务器
      const formData = new FormData();
      formData.append("file", file.file);
      // 开始上传
      this.$axios({
        url: "/upload",
        // post请求
        method: "POST",
        // 添加头信息
        headers: {
          Authorization: this.token
        },
        data: formData
      }).then(res => {
        // url就是图片的路径
        const { url } = res.data.data;
        // 替换掉当前的头像路径
        this.userinfo.head_img = url;
        // 图片上传成功之后调用编辑用户信息的方法
        this.handleEdit({
          head_img: url
        });
      });
    },
    // 编辑信息
    handleEdit(data) {
      return this.$axios({
        url: "/user_update/" + this.userinfo.id,
        method: "POST",
        // 添加头信息
        headers: {
          Authorization: this.token
        },
        data: data
      }).then(res => {
        console.log(res);
        this.$toast.success("修改成功");
      });
    },
    // 修改昵称
    changenickname() {
      const request = this.handleEdit({ nickname: this.nickname });
      request.then(() => {
        this.userinfo.nickname = this.nickname;
      });
    },

    //   修改密码
    changepassword() {
      this.handleEdit({ password: this.password });
      this.userinfo.password = this.password;
      this.show = false;
    },
    // 选择性别
    onSelect(item) {
      console.log(item);
      this.handleEdit({ gender: item.value });
      this.userinfo.gender = item.value;
    }
  },

  components: {
    navbar,
    listbar
  }
};
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  .navbar {
    background-color: #fff;
    .iconjiantou2 {
      color: #333;
    }
    .title {
      color: #333;
    }
  }
  .user_pic {
    position: relative;
    margin-top: 2.333333rem;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3.066667rem;
    .van-uploader {
      position: absolute;
      border-radius: 50%;
      opacity: 0;
    }
    img {
      width: 2rem;
      height: 2rem;
      border-radius: 50%;
    }
  }
}
</style>
